<template>
  <div id="Warehouse_data">
    <div class="topOpt">
      <el-button type="primary" @click="addNew">新增</el-button>
      <!-- <a
        :href="_url2 + 'server/ca_warehouse_data_template_download/'"
        download=""
        >模板下载</a
      > -->
      <el-button type="primary" @click="templateDownload">模板下载</el-button>
      <el-upload
        class="upload-demo"
        action="123"
        :before-upload="beforeUpload"
        :http-request="uploadFile"
        :file-list="fileList"
        :on-success="uploadSuccess"
        :on-remove="handleRemove"
        ref="upload"
      >
        <el-button type="primary">选择文件</el-button>
      </el-upload>

      <el-button type="primary" @click="uploadSubmit">确认上传</el-button>
    </div>
    <div class="table_data">
      <el-table max-height="720" border :data="tableData" style="width: 100%">
        <el-table-column prop="product_id" align="center" label="产品编码">
        </el-table-column>

        <el-table-column prop="sku" align="center" label="sku">
        </el-table-column>
        <el-table-column prop="lengths" align="center" label="长度">
        </el-table-column>
        <el-table-column prop="widths" align="center" label="宽">
        </el-table-column>

        <el-table-column prop="heights" align="center" label="高">
        </el-table-column>

        <el-table-column prop="weights" align="center" label="重量">
        </el-table-column>

        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="text" size="small" @click="editData(scope.row)"
              >编辑</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- <el-pagination
        @current-change="changePage"
        :current-page="currentPage"
        :page-size="50"
        layout="total, prev, pager, next, jumper"
        :total="total_num"
      >
      </el-pagination> -->
    </div>
    <!-- 编辑仓库数据 -->
    <el-dialog title="仓库数据编辑" :visible.sync="dialogVisible" width="40%">
      <el-form
        ref="warehouse_form"
        :rules="rules"
        label-width="100px"
        :inline="true"
        :model="warehouse_form"
      >
        <el-form-item label="sku" prop="sku">
          <el-input v-model="warehouse_form.sku"></el-input>
        </el-form-item>
        <el-form-item label="长度" prop="lengths">
          <el-input v-model="warehouse_form.lengths"></el-input>
        </el-form-item>
        <el-form-item label="宽" prop="widths">
          <el-input v-model="warehouse_form.widths"></el-input>
        </el-form-item>
        <el-form-item label="高" prop="heights">
          <el-input v-model="warehouse_form.heights"></el-input>
        </el-form-item>
        <el-form-item label="重量" prop="weights">
          <el-input v-model="warehouse_form.weights"></el-input>
        </el-form-item>
        <el-form-item label="产品编码" prop="product_id">
          <el-input v-model="warehouse_form.product_id"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button
            class="submitBtn"
            type="primary"
            @click="submitData('warehouse_form')"
            >提交</el-button
          >
        </el-form-item>
      </el-form>
    </el-dialog>
    <!-- 新增仓库数据 -->
    <el-dialog title="新增仓库数据" :visible.sync="dialogVisible1" width="40%">
      <el-form
        ref="add_warehouseform"
        :rules="rules"
        label-width="100px"
        :inline="true"
        :model="warehouse_form_add"
      >
        <el-form-item label="sku" prop="sku">
          <el-input v-model="warehouse_form_add.sku"></el-input>
        </el-form-item>
        <el-form-item label="长度" prop="lengths">
          <el-input v-model="warehouse_form_add.lengths"></el-input>
        </el-form-item>
        <el-form-item label="宽" prop="widths">
          <el-input v-model="warehouse_form_add.widths"></el-input>
        </el-form-item>
        <el-form-item label="高" prop="heights">
          <el-input v-model="warehouse_form_add.heights"></el-input>
        </el-form-item>
        <el-form-item label="重量" prop="weights">
          <el-input v-model="warehouse_form_add.weights"></el-input>
        </el-form-item>
        <el-form-item label="产品编码" prop="product_id">
          <el-input v-model="warehouse_form_add.product_id"></el-input>
        </el-form-item>
        <el-form-item class="last_formItem">
          <el-button
            class="addSubmit"
            type="primary"
            @click="submitData1('add_warehouseform')"
            >提交</el-button
          >
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      fileList: [],
      file: "",
      dialogVisible: false,
      dialogVisible1: false,
      tableData: [],
      warehouse_form: {},
      rules: {
        sku: [{ required: true, message: "请输入sku", trigger: "blur" }],
        lengths: [
          { required: true, message: "请输入长度", trigger: "blur" },
          {
            message: "请输入数字",
            trigger: "blur",
            pattern: /(^\d+$)|(^\d+\.\d+$)/
          }
        ],
        widths: [
          { required: true, message: "请输入宽", trigger: "blur" },
          {
            message: "请输入数字",
            trigger: "blur",
            pattern: /(^\d+$)|(^\d+\.\d+$)/
          }
        ],
        heights: [
          { required: true, message: "请输入高", trigger: "blur" },
          {
            message: "请输入数字",
            trigger: "blur",
            pattern: /(^\d+$)|(^\d+\.\d+$)/
          }
        ],
        weights: [
          { required: true, message: "请输入重量", trigger: "blur" },
          {
            message: "请输入数字",
            trigger: "blur",
            pattern: /(^\d+$)|(^\d+\.\d+$)/
          }
        ]
        // product_id: [
        //   { required: true, message: "请输入产品编码", trigger: "blur" }
        // ]
      },
      // 新增
      warehouse_form_add: {},
      rowData: {}
    };
  },
  methods: {
    uploadSubmit() {
      console.log("确认提交");
      if (this.file === "") {
        alert("请上传文件");
        return;
      }
      let fd = new FormData();
      fd.append("files", this.file);
      let url =
        this._url2 +
        "server/order_management/ca_warehouse_data_template_upload/";
      this.$axios.post(url, fd).then(res => {
        this.fileList = [];
        this.file = "";
        alert(res.data.msg);
      });
    },
    isExecl(fileName) {
      return /(.xlsx|.xls)$/.test(fileName);
    },
    beforeUpload(file) {
      this.fileList = [];
      console.log("之前", file);
      console.log(!this.isExecl(file.name));
      if (!file || (file && !this.isExecl(file.name))) {
        alert("上传后缀为.xlsx,.xls类型的表格文件");
        return;
      }
      this.file = file;
    },
    uploadFile(file) {
      console.log("上传", file);
    },
    uploadSuccess(file) {
      console.log("上传成功", file);
    },
    handleRemove(file) {
      console.log("移除", file);
    },
    // 下载模板
    templateDownload() {
      let url =
        this._url2 +
        "server/order_management/ca_warehouse_data_template_download/";
      this.$axios
        .get(url, {
          responseType: "blob" // 响应类型 blob
        })
        .then(res => {
          let url = window.URL.createObjectURL(new Blob([res.data]));
          let link = document.createElement("a");
          link.href = url;
          link.setAttribute("download", "仓库数据_模板.xlsx");
          document.body.appendChild(link);
          link.click();
        });
    },
    uploadFile() {},
    addNew() {
      this.dialogVisible1 = true;
    },
    getTableData() {
      let url = this._url2 + "server/order_management/ca_warehouse_data/";
      this.$axios.get(url).then(res => {
        this.tableData = res.data.data;
      });
    },
    editData(row) {
      this.rowData = row;
      this.dialogVisible = true;
      for (let key in row) {
        this.$set(this.warehouse_form, key, row[key]);
        // this.warehouse_form[key] = row[key];
      }
    },
    // 编辑
    submitData(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.dialogVisible = false;
          let fd = new FormData();
          for (let key in this.rowData) {
            // this.row[key] = this.warehouse_form[key];
            this.$set(this.rowData, key, this.warehouse_form[key]);
            fd.append(key, this.warehouse_form[key]);
          }
          console.log(fd);
          // 发送请求
          let url =
            this._url2 + "server/order_management/ca_update_warehouse_data/";
          this.$axios.post(url, fd).then(res => {
            if (res.data.code === 200) {
              alert("修改成功");
            } else {
              alert("修改失败,请联系IT处理!");
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // 新增
    submitData1(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.dialogVisible1 = false;
          let fd = new FormData();
          for (let key in this.warehouse_form_add) {
            fd.append(key, this.warehouse_form_add[key]);
          }
          // 方法名需要修改
          let url =
            this._url2 + "server/order_management/ca_insert_warehouse_data/";
          this.$axios.post(url, fd).then(res => {
            if (res.data.code === 200) {
              alert("新增成功");
              this.getTableData();
            } else {
              alert("新增失败,请联系IT处理!");
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  },
  created() {
    this.getTableData();
  }
};
</script>

<style scoped>
#Warehouse_data {
  margin: 22px;
}
#Warehouse_data .topOpt {
  margin-bottom: 22px;
  display: flex;
}
#Warehouse_data .topOpt .el-button {
  margin: 0 20px;
}
</style>
<style>
#Warehouse_data .el-dialog .el-form {
  margin-left: 25px;
}
#Warehouse_data .submitBtn {
  width: 202px;
  margin-left: 100px;
}
#Warehouse_data .last_formItem {
  width: 100%;
  text-align: center;
}
#Warehouse_data input[type="file"] {
  display: none;
}
</style>



